  .site-list__link:active {
    color: #766357;
  }
}
@media (min-width: 768px) {
  .site-list {
    display: grid;
    align-items: center;
    grid-template-columns: repeat(5, 1fr);
    height: 56px;
  }
  .site-list__item:nth-child(4n+3) {
    grid-column: 4 / 5;
  }
  .site-list__item:nth-child(4n+4) {
    grid-column: 5 / 6;
  }
  .site-list__item {
    border: none;
    background: #ffffff;
  }
  .site-list__link {
    color: #000000;
  }
  .site-list__item:last-child::before {
    display: none;
  }
}
