.radio_label {
  font-weight: 700;
  font-size: 18px;
  line-height: 26px;
  display: inline-block;
  padding-left: 63px;
  position: relative;
  padding-top: 11px;
  min-height: 46px;
  cursor: pointer;
}
.radio_label::before {
  content: "";
  position: absolute;
  width: 46px;
  height: 46px;
  display: block;
  border-radius: 50%;
  background: #f2f2f2;
  top: 0;
  left: 0;
}
.radio_label::before:hover {
  background-color: #e5e5e5;
}
.radio_label::before:active {
  background-color: #d5d5d5;
}
.radio_label::after {
  content: "";
  position: absolute;
  width: 16px;
  height: 16px;
  border-radius: 50%;
  background: #000000;
  top: 15px;
  left: 16px;
  display: none;
}
.radio__input:checked + .radio_label::after {
  display: block;
}
