}
.checkbox_label::before {
  content: "";
  position: absolute;
  width: 46px;
  height: 46px;
  display: block;
  border-radius: 4px;
  background: #f2f2f2;
  top: 0;
  left: 0;
}
.checkbox_label::before:hover {
  background-color: #e5e5e5;
}
