  background-color: #d5d5d5;
}
.checkbox_label::after {
  content: "";
  position: absolute;
  width: 22px;
  height: 16px;
  background-image: url("../img/form/tick.svg");
  top: 15px;
  left: 16px;
  display: none;
}
